'use strict'

import axios from 'axios'
import qs from 'qs'
import {
    Loading,
    Message
} from 'element-ui'

var loadinginstace;
axios.defaults.timeout = 5000
axios.defaults.baseURL = process.env.NODE_ENV == "development" ? 'http://localhost:8888/api' : '';
//
let status = false;
axios.interceptors.request.use(config => {
    if (config.url.indexOf('metrics') >= 0) {
        return config;
    }
    if (loadinginstace && loadinginstace.visible && !status) {
        return config;
    } else {
        status = true;
        loadinginstace = Loading.service({
            target: '.content-container',
            lock: true,
            text: "..."
        });
    }
    return config
}, error => {
    if (loadinginstace) {
        loadinginstace.close();
        status = false;
    }
    return Promise.reject('')
})
//response
axios.interceptors.response.use(response => {
    if (loadinginstace) {
        status = false;
        loadinginstace.close();
    }
    if (response.config.url.indexOf('druid') > 0 || response.config.url.indexOf('metrics') > 0) {
        if (response.status == 200) {
            return Promise.resolve(response.data);
        } else {
            return Promise.reject('!!!');
        }
    }
    if (response.data.operationState == 'SUCCESS') { //
        return Promise.resolve(response.data.data)
    } else { //reject
        return Promise.reject(response.data.errors[0]);
    }
}, error => { //http
    if (loadinginstace) {
        loadinginstace.close();
        status = false;
    }
    switch (error.response.status) {
        case 403:
            location.href = '/';
        case 401:
            location.href = '/';
        case 404:
            return Promise.reject("");
        case 500:
            return Promise.reject("")
        case 504:
            return Promise.reject("")
        case 502:
            return Promise.reject("")
        default:
            return Promise.reject(error.response.data.msg[0]);
    }
})


export default {
    /**
     * post(form)
     * @param {string} url 
     * @param {object} data 
     * @param {Function} done 
     * @param {Function} fail ()
     */
    post(url, data, done, fail) {
        return axios({
            method: 'post',
            url,
            data: qs.stringify(data),
            headers: {
                'X-Requested-With': 'XMLHttpRequest',
                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
            }
        }).then(
            data => done(data)
        ).catch(error => {
            if (fail) {
                fail(error)
            } else {
                Message.error({
                    message: error
                });
            }
        })
    },
    /**
     * post(body)
     * @param {string} url 
     * @param {object} data 
     * @param {Function} done 
     * @param {Function} fail ()
     */
    postBody(url, data, done, fail) {
        return axios.post(url, data)
            .then(data => done(data))
            .catch(error => {
                if (fail) {
                    fail(error)
                } else {
                    Message.error({
                        message: error
                    });
                }
            });
    },
    /**
     * get
     * @param {string} url 
     * @param {object} data ()
     * @param {Function} done 
     * @param {Function} fail ()
     */
    get(url, ...options) {
        let params, done, fail;
        if (typeof options[0] === 'object') {
            params = options[0];
            done = options[1];
            fail = options[2];
        } else {
            params = {};
            done = options[0];
            fail = options[1];
        }
        return axios({
            method: 'get',
            url,
            params, // get 
            headers: {
                'X-Requested-With': 'XMLHttpRequest'
            }
        }).then(
            data => done(data)
        ).catch(error => {
            if (fail) {
                fail(error)
            } else {
                Message.error({
                    message: error
                });
            }
        })
    }
}


