<template>
    <section>
        <el-row>
            <el-col :span="6">
                <el-input placeholder="" v-model="searchKey" prefix-icon="el-icon-fa-search">
                    <div slot="append">
                        <el-button type="primary" icon="el-icon-fa-search"
                                   @click=" pager.page = 1 ;doSearch()"></el-button>
                    </div>
                </el-input>
            </el-col>
            <el-col :span="6" :offset="12">
                <el-button type="primary" icon="el-icon-fa-plus" @click="addRole" size="small"></el-button>
            </el-col>
        </el-row>

        <el-table :data="pager.dataList" border stripe style="width: 100%">
            <el-table-column prop="id" label="ID"  header-align="center" align="center" width="55">
            </el-table-column>
            <el-table-column prop="name" label="" show-overflow-tooltip header-align="center" align="center">
            </el-table-column>
            <el-table-column prop="description" label="" show-overflow-tooltip header-align="center" align="center">
            </el-table-column>
            <el-table-column prop="status" label="" show-overflow-tooltip header-align="center" align="center">
                <template slot-scope="scope">
                    <el-tag :type="scope.row.installed  ? 'success' : 'danger'" close-transition>{{scope.row.installed ?
                        '' : ''}}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" show-overflow-tooltip header-align="center" align="center">
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button title="" size="mini" type="primary" icon="el-icon-fa-edit"
                                   @click="handleEdit(scope.$index,scope.row)"></el-button>
                        <el-button title="" v-if="!scope.row.installed" size="mini" type="primary"
                                   icon="el-icon-fa-trash" @click="handleDelete(scope.$index,scope.row)"></el-button>
                        <el-button title="" size="mini" type="primary" icon="el-icon-fa-bolt"
                                   @click="handleGrant(scope.$index,scope.row)"></el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>
        <el-row>
            <el-col :span="6" :offset="18">
                <el-pagination background small style="float:right" layout="prev, pager, next"
                               :total="pager.pager.recordCount" :page-size="pager.pager.pageSize"
                               :current-page.sync="pager.pager.pageNumber" v-show="pager.pager.pageCount != 0"
                               @current-change="changePage">
                </el-pagination>
            </el-col>
        </el-row>
        <!-- -->
        <el-dialog :title="role.id == 0 ? '' : '' " :visible.sync="addEditShow" width="30%">
            <el-form :model="role" :rules="$rules" ref="roleForm">
                <el-form-item label="" :label-width="formLabelWidth" prop="name">
                    <el-input v-model="role.name" auto-complete="off" placeholder="" suffix-icon="el-icon-fa-vcard" ></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="description">
                    <el-input type="textarea" :maxlength="500"
                    :autosize="{ minRows: 4, maxRows: 8}" v-model="role.description" auto-complete="off" placeholder="" suffix-icon="el-icon-fa-file-word-o"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addEditShow = false ; user = {installed:false}"> </el-button>
                <el-button type="primary" @click="saveOrUpdateRole('roleForm')"> </el-button>
            </div>
        </el-dialog>

        <el-dialog title="" :visible.sync="grantShow" :width="dialogWidth">
            <template>
                <el-transfer v-model="selected" :data="options" :titles="['', '']" filterable></el-transfer>
            </template>
            <div slot="footer" class="dialog-footer">
                <el-button @click="grantShow = false"> </el-button>
                <el-button type="primary" @click="grant"> </el-button>
            </div>
        </el-dialog>
    </section>
</template>
<style>

</style>
<script>
export default {
  data() {
    return {
      searchKey: "",
      pager: {
        pager: {
          pageCount: 0,
          pageNumber: 1,
          pageSize: 15,
          recordCount: 0
        }
      },
      selected: [],
      options: [],
      addEditShow: false,
      grantShow: false,
      role: {
        id: 0,
        name: "",
        description: "",
        installed: false
      },
      formLabelWidth: "120px"
    };
  },
  computed: {
    dialogWidth() {
      return 590 * 100 / this.$utils.windowWidth() + "%";
    }
  },
  watch: {
    options: function() {
      this.selected = [];
      this.options.forEach(item => {
        if (item.selected) {
          this.selected.push(item.key);
        }
      });
    }
  },
  methods: {
    addRole() {
      this.role = {
        id: 0,
        name: "",
        description: "",
        installed: false
      };
      this.addEditShow = true;
    },
    grant() {
      this.$api.Role.grant(this.role.id, this.selected, result => {
        this.$message({
          type: "success",
          message: "!"
        });
        window.setTimeout(() => {
          this.grantShow = false;
        }, 2000);
      });
    },
    changePage() {
      if (this.searchKey) {
        this.doSearch();
      } else {
        this.loadData();
      }
    },
    doSearch() {
      this.$api.Role.search(
        this.pager.pager.pageNumber,
        this.searchKey,
        result => {
          this.pager = result.pager;
        }
      );
    },
    saveOrUpdateRole(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var callback = result => {
            this.changePage();
            this.addEditShow = false;
          };
          this.role.id
            ? this.$api.Role.update(this.role, callback)
            : this.$api.Role.save(this.role, callback);
        } else {
          return false;
        }
      });
    },
    handleEdit(index, row) {
      this.role = row;
      this.addEditShow = true;
    },
    handleGrant(index, row) {
      this.role.id = row.id;
      this.$api.Role.roleGrantInfo(row.id, result => {
        this.options = [];
        result.infos.forEach((item, index) => {
          this.options.push({
            key: item.id,
            label: item.description,
            selected: item.selected
          });
        });
        this.grantShow = true;
      });
    },
    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$api.Role.delete(row.id, result => {
          this.$message({
            type: "success",
            message: "!"
          });
          window.setTimeout(() => {
            this.changePage();
          }, 2000);
        });
      });
    },
    loadData() {
      this.$api.Role.list(this.pager.pager.pageNumber, result => {
        this.pager = result.pager;
      });
    }
  },
  created: function() {
    this.loadData();
  }
};
</script>


