import http from '@/http'

export default {
	   load(success) {
	        http.get('user/load', success);
	    },
	/**
	 * 
	 * 
	 * @param {Object}
	 *            loginForm 
	 * @param {*}
	 *            success 
	 */
    login(loginForm, success) {
        http.postBody('user/login', loginForm, success);
    },
    /**
	 * 
	 * 
	 * @param {number}
	 *            page 
	 * @param {Function}
	 *            success 
	 */
    list(page, success) {
        http.get('user/list', {
            page: page
        }, success);
    },
    /**
	 * 
	 * 
	 * @param {number}
	 *            page 
	 * @param {string}
	 *            key 
	 * @param {Function}
	 *            success 
	 */
    search(page, key, success) {
        http.get('user/search', {
            page: page,
            key: key
        }, success);
    },
    /**
	 * 
	 * 
	 * @param {Object}
	 *            user 
	 * @param {Function}
	 *            success 
	 */
    save(user, success) {
        http.postBody('user/add', user, success)
    },
    /**
	 * 
	 * 
	 * @param {Object}
	 *            user 
	 * @param {Function}
	 *            success 
	 */
    update(user, success) {
        http.postBody('user/edit', user, success)
    },
    /**
	 * 
	 * 
	 * @param {number}
	 *            id id
	 * @param {Function}
	 *            success 
	 */
    delete(id, success) {
        http.get('user/delete/' + id, success);
    },
    /**
	 * 
	 * 
	 * @param {number}
	 *            id id
	 * @param {string}
	 *            name 
	 * @param {string}
	 *            password 
	 * @param {Function}
	 *            success 
	 */
    resetPassword(id, name, password, success) {
        http.postBody("/user/resetPassword", {
            id: id,
            name: name,
            password: password
        }, success);
    },
    /**
	 * 
	 * 
	 * @param {string}
	 *            type (role/permission)
	 * @param {number}
	 *            id id
	 * @param {Function}
	 *            success 
	 */
    userGrantInfo(type, id, success) {
        let url = "/user/" + type + "/" + id;
        http.get(url, success);
    },
    /**
	 * 
	 * 
	 * @param {number}
	 *            id id
	 * @param {string}
	 *            type (role/permission)
	 * @param {Array}
	 *            powers /
	 * @param {Function}
	 *            success 
	 */
    grant(id, type, powers, success) {
        http.postBody('user/grant/' + type, {
            id: id,
            grantIds: powers
        }, success)
    },
    /**
	 * 
	 * 
	 * @param {Function}
	 *            success 
	 */
    logout(success) {
        http.get('user/logout', success)
    }
}


