import http from '@/http'

export default {
    /**
     * 
     * @param {number} page  
     * @param {Function} success 
     */
    list(page, success) {
        http.get('role/list', {
            page: page
        }, success);
    },
    /**
     * 
     * @param {number} page 
     * @param {string} key 
     * @param {Function} success 
     */
    search(page, key, success) {
        http.get('role/search', {
            page: page,
            key: key
        }, success);
    },
    /**
     * 
     * @param {Object} role 
     * @param {Function} success 
     */
    save(role, success) {
        http.postBody('role/add', role, success)
    },
    /**
     * 
     * @param {Object} role 
     * @param {Function} success 
     */
    update(role, success) {
        http.postBody('role/edit', role, success)
    },
    /**
     * 
     * @param {number} id id
     * @param {Function} success 
     */
    roleGrantInfo(id, success) {
        http.get('role/permission/' + id, success);
    },
    /**
     * 
     * @param {number} id id
     * @param {Array} powers 
     * @param {Function} success 
     */
    grant(id, powers, success) {
        http.postBody('role/grant/', {
            id: id,
            grantIds: powers
        }, success)
    },
    /**
     * 
     * @param {number} id id
     * @param {*} success 
     */
    delete(id, success) {
        http.get('role/delete/' + id, success);
    }
}


