var validateMobile = (rule, value, callback) => {
    if (!value) {
        callback(new Error(''));
    } else if (!/^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/.test(value)) {
        callback(new Error(''));
    } else {
        callback();
    }
};
var validateEmail = (rule, value, callback) => {
    if (/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(value)) {
        callback();
    } else {
        callback(new Error(""));
    }
};
export default {
    mobile: [{
        validator: validateMobile,
        trigger: "blur"
    }],
    name: [{
        required: true,
        message: "",
        trigger: "blur"
    }],
    realName: [{
        required: true,
        message: "",
        trigger: "blur"
    }],
    password: [{
            required: true,
            message: "",
            trigger: "blur"
        },
        {
            min: 8,
            max: 16,
            message: "816",
            trigger: "blur"
        }
    ],
    rePassword: [{
            required: true,
            message: "",
            trigger: "blur"
        },
        {
            min: 8,
            max: 16,
            message: "816",
            trigger: "blur"
        },
    ],
    phone: [{
            required: true,
            message: "",
            trigger: "blur"
        },
        {
            validator: validateMobile,
            trigger: "blur"
        }
    ],
    email: [{
            required: true,
            message: "",
            trigger: "blur"
        },
        {
            validator: validateEmail,
            trigger: "blur"
        }
    ],
    description: [{
        required: true,
        message: "",
        trigger: "blur"
    }],
    groupId: [{
        type: "number",
        required: true,
        message: "",
        trigger: "blur"
    }],
    value: [{
        required: true,
        message: "",
        trigger: "blur"
    }]
}


